@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;700&display=swap);
.body{
    margin: 0%;
    padding: 0%;
    background-color: #f0eded;
    /* width: 1366px; */
  }
  
  *{
    font-family: 'Open Sans', sans-serif;
  }


.navbar{
    display: flex;
    background-color: var(--white);
    font-weight: bolder;
 align-items: center;
    justify-content: space-evenly;
    box-shadow: 0px 2px 5px 0px rgb(110, 110, 110);
    height: calc(25px + 3vw);
    position: -webkit-sticky;
    position: sticky;
    border-radius: 2px;
}

.toggle_navbar{
    display: none;
}

.navbar .logo{
    color: #0d1c6b;
    font-family: 'Open Sans', sans-serif;
    font-size: calc(8px + 1.6vw);
    font-weight: bolder;
}

.navbar_options{
    display: flex;

}

.navbar_option{
    margin: auto calc(10px + 1vw);
    font-family: 'Open Sans', sans-serif;
    font-size: calc(6px + 1.1vw);
}

.navbar_option a{
    text-decoration: none;
    color: var(--black);
}

.navbar_option a:hover{
    color:  var(--sky);
}

.searchbar{
    position: relative;
    color: var(--white);
}

#searchbar{
  border: none;
  background-color: var(--sky);
  height: 2.3vw;
  min-height: 20px;
  border-radius: 5px;
  width: calc(10px + 19vw);
  padding:0  5px;

}
#searchbar::-webkit-input-placeholder{
    color: var(--white) ;
    padding: 5px;
    font-size: calc(2px + 0.8vw );
}
#searchbar:-ms-input-placeholder{
    color: var(--white) ;
    padding: 5px;
    font-size: calc(2px + 0.8vw );
}
#searchbar::placeholder{
    color: var(--white) ;
    padding: 5px;
    font-size: calc(2px + 0.8vw );
}

#searchbar:focus{
    outline: none;
}

.search_icon{
  position: absolute;
   right: 2%;
   top: 20%;
}
.profile_image img{
    width: calc(10px + 3vw);
    height: calc(10px + 2.5vw);
    border-radius: 50%;

}

/* @media (max-width:600px) {
    #searchbar::placeholder{
     visibility: hidden;
    }
    
} */

@media (max-width:450px) {
    .navbar{
        display: none;
    }

    .toggle_navbar{
display:flex;
flex-direction: row;
background-color: var(--white);
font-weight: bolder;
align-items: center;
justify-content: space-evenly;
box-shadow: 0px 2px 5px 0px rgb(110, 110, 110);
height: calc(25px + 3vw);
position: -webkit-sticky;
position: sticky;
border-radius: 2px;
    }
    

    .toggle_options{
        position: absolute;
        background-color: var(--white);
        left: 0%;
        padding-left: 2vw;
        width:100vw;
    }
    .toggle_option:hover{
          color: var(--grey);
    }
    .toggle_logo{
        color: #0d1c6b;
        font-family: 'Open Sans', sans-serif;
        font-size: calc(5px + 4vw);
        font-weight: bolder;
    }
    .toggle_searchicon, .cart_option{
        padding:0 1vw;

    }
}

.header{
 background-color:  var(--white);   
 line-height: calc(15px + 2vw);
 border-radius: 3px;
 box-shadow: 0px 1px 7px 1px rgb(194, 192, 192);
 width: 80vw;
 margin: 0% auto;
 font-family: 'Open Sans', sans-serif;
}

.header_list{
    display: flex;
  align-items: center;
  justify-content:flex-start;
  margin: 0%;
}

.header_list_options{
    list-style: none;
    margin: auto 1.5vw;
    font-size: calc(3px + 1vw);
    font-weight: 540;
}
@media (max-width:450px){
  .header{
    width: 100vw;
    line-height: calc(10px + 5vw);
  
   }
   .header_list_options{
    font-size: calc(3px + 2vw);
   }

   .header_list{
    display: flex;
  align-items: center;
  justify-content:flex-start;
  padding: 0%;
  margin: 0%;
}

}
.content_container{
    margin: 20px auto;
    width:80vw ;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 4vw;
}
.left_container{
    width: calc(20px + 25vw);
}

.left_bottom_container img{
    width:calc(10px + 20vw);
}
.video_heading{
    margin: 10px auto;
    padding: 5px;
    font-size: calc(5px + 1.2vw);
    font-weight:600;
    font-family: 'Open Sans', sans-serif;
}

.video_link{
    width: calc(10px + 48vw) !important;
    height: calc(120px + 17vw) !important;

}

.change_options{
    display: flex;
    justify-content: space-between;
    margin: 2vw;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    font-size: calc(3px + 1vw);
}
.previous_option:hover, .next_option:hover{
    color: var(--grey);
}
.next_option{
    margin-right: 3vw;
}

@media (max-width:450px) {
    .content_container{
        margin:  0px ;
      display: flex;
        flex-direction: column-reverse;
    
    }
    .left_container{
        width:98vw;
    }
    .right_container{
        width: 100vw;
    }
    .video_heading{
text-align: center;
        font-size: calc(5px + 2vw);
        background-color: var(--white);
     box-sizing: border-box;
     margin: 5px auto 0px auto;
    }
    .video_link{
        width: calc(0px + 100vw) !important;
        height: calc(120px + 20vw) !important;
    
    }
    .change_options{
    
        justify-content: space-between;
        font-weight: 600;
        font-family: 'Open Sans', sans-serif;
        font-size: calc(3px + 2vw);
    }

}
.content_button{
    background-color: var(--blue);
    line-height: calc(2px + 2.3vw);
    margin: 10px auto;
    color: var(--white);
    padding: 10px;
    font-size: calc(5px + 1vw);
  border-radius: 2px;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  transition: all ease 0.3s;

}

@media (max-width:450px) {
  .content_button{
    line-height: calc(3px + 3vw);

    padding: 10px;
    font-size: calc(5px + 2.5vw);
  border-radius: 5px;


}
}


.toggle_block{
    background-color: var(--white);
    color: var(--black);
}
.left_top{
    background-color: var(--white);
  margin-top: calc(20px + 3vw);
  box-shadow: 1px 1px 5px 3px rgb(202, 202, 202);
}

.content_heading{
    background-color: var(--light_black);
    line-height: calc(2px + 1.8vw);
    font-family: 'Open Sans', sans-serif;
    margin: 1.5vw auto;
    color: var(--white);
    padding: 10px;
    font-weight: bold;
    text-align: center;
    font-size: calc(5px + 1.5vw);
}

@media (max-width:450px) {

  .left_top{
  margin-top: calc(2px + 1vw);
}
  .content_heading{
    line-height: calc(4px + 3.5vw);
    margin: 1.5vw auto;
    padding: 10px;
    font-weight: bold;
    text-align: center;
    font-size: calc(5px + 3.5vw);
}
}
:root{
    --blue: #0d1c6b;
  --white: #ffffff;
  --black: #000000;
  --light_black: #4d4d4d;
  --grey: #b3b3b3;
  --off_white: #f0eded;
  --fade_sky: #b6bfd3;
  --sky: #b3caff;
}


*{
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

body{
    margin: 0%;
    padding: 0%;
  
}
