.left_top{
    background-color: var(--white);
  margin-top: calc(20px + 3vw);
  box-shadow: 1px 1px 5px 3px rgb(202, 202, 202);
}

.content_heading{
    background-color: var(--light_black);
    line-height: calc(2px + 1.8vw);
    font-family: 'Open Sans', sans-serif;
    margin: 1.5vw auto;
    color: var(--white);
    padding: 10px;
    font-weight: bold;
    text-align: center;
    font-size: calc(5px + 1.5vw);
}

@media (max-width:450px) {

  .left_top{
  margin-top: calc(2px + 1vw);
}
  .content_heading{
    line-height: calc(4px + 3.5vw);
    margin: 1.5vw auto;
    padding: 10px;
    font-weight: bold;
    text-align: center;
    font-size: calc(5px + 3.5vw);
}
}