.content_container{
    margin: 20px auto;
    width:80vw ;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 4vw;
}
.left_container{
    width: calc(20px + 25vw);
}

.left_bottom_container img{
    width:calc(10px + 20vw);
}
.video_heading{
    margin: 10px auto;
    padding: 5px;
    font-size: calc(5px + 1.2vw);
    font-weight:600;
    font-family: 'Open Sans', sans-serif;
}

.video_link{
    width: calc(10px + 48vw) !important;
    height: calc(120px + 17vw) !important;

}

.change_options{
    display: flex;
    justify-content: space-between;
    margin: 2vw;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    font-size: calc(3px + 1vw);
}
.previous_option:hover, .next_option:hover{
    color: var(--grey);
}
.next_option{
    margin-right: 3vw;
}

@media (max-width:450px) {
    .content_container{
        margin:  0px ;
      display: flex;
        flex-direction: column-reverse;
    
    }
    .left_container{
        width:98vw;
    }
    .right_container{
        width: 100vw;
    }
    .video_heading{
text-align: center;
        font-size: calc(5px + 2vw);
        background-color: var(--white);
     box-sizing: border-box;
     margin: 5px auto 0px auto;
    }
    .video_link{
        width: calc(0px + 100vw) !important;
        height: calc(120px + 20vw) !important;
    
    }
    .change_options{
    
        justify-content: space-between;
        font-weight: 600;
        font-family: 'Open Sans', sans-serif;
        font-size: calc(3px + 2vw);
    }

}