.header{
 background-color:  var(--white);   
 line-height: calc(15px + 2vw);
 border-radius: 3px;
 box-shadow: 0px 1px 7px 1px rgb(194, 192, 192);
 width: 80vw;
 margin: 0% auto;
 font-family: 'Open Sans', sans-serif;
}

.header_list{
    display: flex;
  align-items: center;
  justify-content:flex-start;
  margin: 0%;
}

.header_list_options{
    list-style: none;
    margin: auto 1.5vw;
    font-size: calc(3px + 1vw);
    font-weight: 540;
}
@media (max-width:450px){
  .header{
    width: 100vw;
    line-height: calc(10px + 5vw);
  
   }
   .header_list_options{
    font-size: calc(3px + 2vw);
   }

   .header_list{
    display: flex;
  align-items: center;
  justify-content:flex-start;
  padding: 0%;
  margin: 0%;
}

}