

.navbar{
    display: flex;
    background-color: var(--white);
    font-weight: bolder;
 align-items: center;
    justify-content: space-evenly;
    box-shadow: 0px 2px 5px 0px rgb(110, 110, 110);
    height: calc(25px + 3vw);
    position: sticky;
    border-radius: 2px;
}

.toggle_navbar{
    display: none;
}

.navbar .logo{
    color: #0d1c6b;
    font-family: 'Open Sans', sans-serif;
    font-size: calc(8px + 1.6vw);
    font-weight: bolder;
}

.navbar_options{
    display: flex;

}

.navbar_option{
    margin: auto calc(10px + 1vw);
    font-family: 'Open Sans', sans-serif;
    font-size: calc(6px + 1.1vw);
}

.navbar_option a{
    text-decoration: none;
    color: var(--black);
}

.navbar_option a:hover{
    color:  var(--sky);
}

.searchbar{
    position: relative;
    color: var(--white);
}

#searchbar{
  border: none;
  background-color: var(--sky);
  height: 2.3vw;
  min-height: 20px;
  border-radius: 5px;
  width: calc(10px + 19vw);
  padding:0  5px;

}
#searchbar::placeholder{
    color: var(--white) ;
    padding: 5px;
    font-size: calc(2px + 0.8vw );
}

#searchbar:focus{
    outline: none;
}

.search_icon{
  position: absolute;
   right: 2%;
   top: 20%;
}
.profile_image img{
    width: calc(10px + 3vw);
    height: calc(10px + 2.5vw);
    border-radius: 50%;

}

/* @media (max-width:600px) {
    #searchbar::placeholder{
     visibility: hidden;
    }
    
} */

@media (max-width:450px) {
    .navbar{
        display: none;
    }

    .toggle_navbar{
display:flex;
flex-direction: row;
background-color: var(--white);
font-weight: bolder;
align-items: center;
justify-content: space-evenly;
box-shadow: 0px 2px 5px 0px rgb(110, 110, 110);
height: calc(25px + 3vw);
position: sticky;
border-radius: 2px;
    }
    

    .toggle_options{
        position: absolute;
        background-color: var(--white);
        left: 0%;
        padding-left: 2vw;
        width:100vw;
    }
    .toggle_option:hover{
          color: var(--grey);
    }
    .toggle_logo{
        color: #0d1c6b;
        font-family: 'Open Sans', sans-serif;
        font-size: calc(5px + 4vw);
        font-weight: bolder;
    }
    .toggle_searchicon, .cart_option{
        padding:0 1vw;

    }
}
