.content_button{
    background-color: var(--blue);
    line-height: calc(2px + 2.3vw);
    margin: 10px auto;
    color: var(--white);
    padding: 10px;
    font-size: calc(5px + 1vw);
  border-radius: 2px;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  transition: all ease 0.3s;

}

@media (max-width:450px) {
  .content_button{
    line-height: calc(3px + 3vw);

    padding: 10px;
    font-size: calc(5px + 2.5vw);
  border-radius: 5px;


}
}

